import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import { Carousel } from "antd";

// 
import Top from "./top";
import withAxios from "../../hoc/withAxios";
import "./index.css";
// loading
import Loading from "../loading";
class Home extends Component {
  constructor() {
    super();
    this.state = {
      saleNum: [],
      saleNumber: [],
      nav: [
        {
          text: "",
          gc_id_1: "1378"
        },
        {
          text: "",
          gc_id_1: "1383"
        },
        {
          text: "",
          gc_id_1: "1379"
        },
        {
          text: "",
          gc_id_1: "1382"
        },
        {
          text: "",
          gc_id_1: "1384"
        },
        {
          text: "",
          gc_id_1: "1309"
        },
        {
          text: "",
          gc_id_1: "1383"
        },

        {
          text: "",
          gc_id_1: "1381"
        }
      ]
    };
    // console.log(this);
  }
  skip(val) {
    // console.log(this);
    // console.log(val);
    this.props.history.push({
      pathname: "/list",
      search: `?gc_id_1=${val}`,
      state: {
        text: val
      }
    });
  }
  // 
  addGoods = id => {
    this.props.history.push({
      pathname: "/goods",
      search: "id=" + id
    });
  };
  // 
  componentWillMount() {
    let { axios } = this.props;
    axios
      .post("/home", {
        a: "saleNum",
        num: 500
      })
      .then(res => {
        this.setState({
          saleNum: res.data
        });
        // console.log(this.state.saleNum);
        // ul
        let vw = 155 * this.state.saleNum.length;

        this.refs.ul_box.style.width = (vw + 15) / 50 + "rem";
        // console.log(vw);
      });
    // 
    setTimeout(() => {
      axios
        .post("/home", {
          a: "saleNum",
          num: 300
        })
        .then(res => {
          this.setState({
            saleNumber: res.data
          });
          this.forceUpdate();
          // console.log(this.state.saleNumber);
        });
    }, 1000);
  }
  shouldComponentUpdate(nextProps, nextState) {
    if (nextState.saleNum == this.state.saleNum) {
      return false;
    } else {
      return true;
    }
  }
  render() {
    // let path = this.props.location.pathname;
    // console.log(this.state);
    return (
      <div className="home">
        <Top />
        console.console.log();
        {/*  */}
        {this.state.saleNum.length != 0 ? (
          <main className="scroll_wrap">
            <div
              style={{
                position: "relative",
                overflow: "hidden"
              }}
            >
              <div className="Box">
                {/*  */}
                <Carousel autoplay>
                  <div>
                    <h3>
                      <img
                        src={require("../../images/s0_05887823099743207.jpg")}
                        alt=""
                      />
                    </h3>
                  </div>
                  <div>
                    <h3>
                      <img
                        src={require("../../images/s0_06064954048617157.jpg")}
                        alt=""
                      />
                    </h3>
                  </div>
                  <div>
                    <h3>
                      <img
                        src={require("../../images/s0_06065063469953376.jpg")}
                        alt=""
                      />
                    </h3>
                  </div>
                  <div>
                    <h3>
                      <img
                        src={require("../../images/s0_06071963883756446.jpg")}
                        alt=""
                      />
                    </h3>
                  </div>
                  <div>
                    <h3>
                      <img
                        src={require("../../images/s0_06074671247457566.jpg")}
                        alt=""
                      />
                    </h3>
                  </div>
                </Carousel>
                {/*  */}
                <nav className="menu-list">
                  <ul className="category_index  clearfix">
                    {this.state.nav.map(item => {
                      return (
                        <li
                          key={item.text}
                          onClick={this.skip.bind(this, item.gc_id_1)}
                        >
                          <i className="icon icon-culture" />
                          <p>{item.text}</p>
                        </li>
                      );
                    })}
                  </ul>
                </nav>
                {/*   */}
                <div className="main-container2" style={{ width: "100%" }}>
                  {/* 1 */}
                  <div className="voucher-img mid-recommend">
                    <img src={require("../../images/z112.png")} alt="" />
                  </div>
                  {/*  */}
                  <div className="yuan">
                    <div className="yuan-top free">
                      <i className="one" />
                      <span className="one"></span>
                      <span className="three"></span>
                      <i className="two" />
                      <div className="yuan-bot">
                        <div className="active-con" />
                      </div>
                    </div>
                    <div className="yuan-bot">
                      <div className="active-con">
                        <div className="wrap">
                          <ul className="clearfix " ref="ul_box">
                            {this.state.saleNum.map(item => {
                              return (
                                <li
                                  key={item.goods_id}
                                  onClick={this.addGoods.bind(
                                    this,
                                    item.goods_id
                                  )}
                                >
                                  <i>
                                    <span>{item.goods_fic_salenum}</span>
                                  </i>
                                  <img src={item.gImg_240} alt="" />
                                  <p>{item.gTitle}</p>
                                  <p
                                    style={{
                                      textAlign: "left"
                                    }}
                                  >
                                    <span
                                      style={{
                                        fontSize: "0.34rem",
                                        color: "#d33d3c",
                                        marginRight: "0.2rem"
                                      }}
                                    >
                                      {item.xianshi_price}
                                    </span>
                                    <b>  {item.gPrice}</b>
                                  </p>
                                </li>
                              );
                            })}

                            {/* <li>
                            <i>
                              <span>12</span>
                            </i>
                            <img
                              src="https://www.aizhiyi.com/data/upload/shop/store/goods/89/2018/11/09/89_05951032489879724_240.jpg?v=14"
                              alt=""
                            />
                            <p></p>
                            <p
                              style={{
                                textAlign: "left"
                              }}
                            >
                              <span
                                style={{
                                  fontSize: "0.34rem",
                                  color: "#d33d3c",
                                  marginRight: "0.2rem"
                                }}
                              >
                                0
                              </span>
                              <b>  68.00</b>
                            </p>
                          </li> */}
                          </ul>
                        </div>
                      </div>
                    </div>
                  </div>
                  {/*  */}
                  <div className="culture-con guess_contain">
                    <header className="title goods_type">
                      <span></span>
                    </header>
                    {/*  */}
                    <ul className="perfect-recom clearfix">
                      {this.state.saleNumber.map(item => {
                        return (
                          <li
                            key={item.goods_id}
                            onClick={this.addGoods.bind(this, item.goods_id)}
                          >
                            <img src={item.gImg_240} alt="" />
                            <h2>{item.gTitle}</h2>
                            <div className="bottom_price clearfix">
                              <span>
                                <dfn></dfn>
                                {item.xianshi_price}
                              </span>
                              <font>: {item.goods_fic_salenum}</font>
                            </div>
                          </li>
                        );
                      })}
                      {/* <li>
                      <img
                        src="https://www.aizhiyi.com/data/upload/shop/store/goods/118/2019/03/22/118_06065910278569319_240.jpg?v=14"
                        alt=""
                      />
                      <h2></h2>
                      <div className="bottom_price clearfix">
                        <span>
                          <dfn></dfn>298.00
                        </span>
                        <font>: 647</font>
                      </div>
                    </li> */}
                    </ul>
                  </div>
                </div>
              </div>
            </div>
          </main>
        ) : (
          <Loading />
        )}
      </div>
    );
  }
}
withRouter(Home);
Home = withAxios(Home);
export default Home;
