import React, { Component } from "react";
import "./index.css";
import { Icon } from "antd";
import withAxios from "../../hoc/withAxios";
import Loading from "../loading";
class Goods extends Component {
  constructor() {
    super();
    this.state = {
      goods: ""
    };
  }
  componentWillMount() {
    let id = this.props.location.search.split("?")[1].split("=")[1];
    let { axios } = this.props;
    axios
      .get("/home", {
        params: {
          a: "goods",
          id: id
        }
      })
      .then(res => {
        this.setState({
          goods: res.data[0]
        });
      });
  }
  shouldComponentUpdate(nextProps, nextState) {
    if (nextState.goods === this.state.goods) {
      return false;
    } else {
      return true;
    }
  }
  return = () => {
    this.props.history.goBack("key");
  };
  addCart = ev => {
    let { axios } = this.props;
    let {
      goods_id,
      gShop,
      gImg,
      gTitle,
      gDescribe,
      gPrice,
      gNumber
    } = this.state.goods;
    axios
      .post("/home", {
        a: "addCart",
        goods_id,
        gShop,
        gImg,
        gTitle,
        gDescribe,
        gPrice,
        gNumber
      })
      .then(res => {
        this.refs.cart.style.color = "red";
      });
  };
  render() {
    console.log(this.state);
    let {
      gTitle,
      gDescribe,
      gImg_240,
      xianshi_price,
      goods_fic_salenum
    } = this.state.goods;
    return (
      <div
        className="goods"
        style={{
          display: "Flex",
          flexDirection: "column"
        }}
      >
        <div className="Top">
          <div className="header-l" onClick={this.return}>
            <Icon
              type="left"
              style={{
                fontSize: "22px",
                color: "#fff"
              }}
            />
          </div>
          <ul className="header-nav">
            <li className="cur"></li>
          </ul>
          <div className="Top_right">
            <Icon type="shopping-cart" />
          </div>
        </div>
        {this.state.goods !== "" ? (
          <main className="main">
            {/*  */}
            <div className="img_Box">
              <img src={gImg_240} alt="" />
            </div>
            <div className="goods-detail-cnt">
              <div className="goods-detail-name">
                <span> {gTitle} </span>
                <p>{gDescribe}</p>
              </div>
              <div className="goods-detail-price">
                <p>
                  <span className="fl">
                    <em>{xianshi_price}</em>
                  </span>
                  <span className="discount-test fl">
                    <span className="discounts">995</span>
                  </span>
                </p>
              </div>
              {/*  */}
              <div className="shipp cfl">
                <div className="goods-detail-item active cfl ">
                  <span className="fl"></span>
                  <span
                    style={{
                      float: "right"
                    }}
                  >
                     : {goods_fic_salenum}
                  </span>
                </div>
              </div>
            </div>
          </main>
        ) : (
          <Loading />
        )}

        <div className="bottom">
          <div className="otreh-handle">
            <li>
              <Icon type="shop" className="tu" />
              <span></span>
            </li>
            <li>
              <Icon type="customer-service" className="tu" />
              <span></span>
            </li>
            <li>
              <Icon type="heart" className="tu" />
              <span></span>
            </li>
          </div>
          <div className="buy-handle">
            <span onClick={this.addCart.bind(this)} ref="cart">
              
            </span>
            <span></span>
          </div>
        </div>
      </div>
    );
  }
}
Goods = withAxios(Goods);
export default Goods;
